export class VacationModel {
	id: number;
	startDate: string;
	vacationPeriodDays: number;
	vacationDetails: string;
	userId: number;
	vacationStatusId: number;
	adminReply: string;

	clear() {
		this.id = 0;
		this.userId = 0;
		this.vacationStatusId = 0;
		this.adminReply = '';
		this.vacationPeriodDays = 0;
		this.vacationDetails = '';
		this.startDate = new Date().toISOString().substr(0, 16);
	}
}
