export class UserReadingModel {
	id: number;
	gpNumber: string;
	shift: string;
	reading: string;
	readingDate: string;

	clear() {
		this.id = 0;
		this.gpNumber = '';
		this.shift = '';
		this.reading = '';
		this.readingDate = new Date().toISOString().substr(0, 16);
	}

	initialize() {
		this.id = 1;
		this.gpNumber = '1';
		this.shift = '1';
		this.reading = '200';
		this.readingDate = new Date().toISOString().substr(0, 16);
	}
}
