export class AdvancedPaymentModel {
	id: number;
	advancedDetails: string;
	amount: string;
	userId: number;
	date: string;
	advancedPaymentStatusId: number;
	adminReply: string;

	clear() {
		this.id = 0;
		this.userId = 0;
		this.advancedPaymentStatusId = 0;
		this.adminReply = '';
		this.advancedDetails = '';
		this.amount = '';
		this.date = new Date().toISOString().substr(0, 16);
	}
}
