// Angular
import {Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
// Material
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
// RxJS
import {Subscription, of} from 'rxjs';
import {delay} from 'rxjs/operators';
// NGRX
import {Update} from '@ngrx/entity';
import {Store, select} from '@ngrx/store';
// State
import {AppState} from '../../../../../core/reducers';
// CRUD
import {LayoutUtilsService, TypesUtilsService} from '../../../../../core/_base/crud';
// Services and Models
import {selectCustomersActionLoading} from '../../../../../core/e-commerce';
import {VacationModel} from '../models/vacation.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'vacation-edit-dialog',
	templateUrl: 'vacation-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class VacationEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	Vacation: VacationModel;
	VacationForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param typesUtilsService: TypesUtilsService
	 */
	constructor(public dialogRef: MatDialogRef<VacationEditDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private fb: FormBuilder,
				private store: Store<AppState>,
				private layoutUtilsService: LayoutUtilsService,
				private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.Vacation = this.data.reading;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}

		this.subscriptions.forEach(el => el.unsubscribe());
	}

	createForm() {
		this.VacationForm = this.fb.group({
			id: [this.Vacation.id, Validators.required],
			vacationPeriodDays: [this.Vacation.vacationPeriodDays, Validators.required],
			vacationDetails: [this.Vacation.vacationDetails, Validators.required],
			startDate: [this.Vacation.startDate, Validators.required],
			vacationStatusId: [this.Vacation.vacationStatusId, this.Vacation.id > 0 ? Validators.required : ''],
			adminReply: [this.Vacation.adminReply, this.Vacation.id > 0 ? Validators.required : ''],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.Vacation.id > 0) {
			return `تعديل الحركة`;
		}

		return 'اضافة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.VacationForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): VacationModel {
		const controls = this.VacationForm.controls;
		const _Vacation = new VacationModel();
		_Vacation.id = this.Vacation.id;
		_Vacation.vacationDetails = controls.vacationDetails.value;
		_Vacation.startDate = controls.startDate.value;
		_Vacation.vacationPeriodDays = controls.vacationPeriodDays.value;
		if (this.Vacation.id > 0) {
			_Vacation.adminReply = controls.adminReply.value;
			_Vacation.vacationStatusId = controls.vacationStatusId.value;
		}

		return _Vacation;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.VacationForm.controls;

		/** check form */
		if (this.VacationForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel price
	 *
	 * @param _Vacation: FuelPricesModel
	 */
	updateReading(_Vacation: VacationModel) {
		this.http.put(
			globalVariables.baseUrl + 'api/vacations',
			{
				..._Vacation
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_Vacation, isEdit: true});
			}, err => {
				let msg = 'فشل في تعديل الاجازة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Fuel price
	 *
	 * @param _Vacation: FuelPricesModel
	 */
	createReading(_Vacation: VacationModel) {
		this.http.post(
			globalVariables.baseUrl + 'api/vacations',
			{
				..._Vacation
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_Vacation, isEdit: false});
			}, err => {
				let msg = 'فشل في اضافة الاجازة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
