// Angular
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import {Store} from '@ngrx/store';
import {AppState} from '../../../core/reducers';
// Lodash
import { shuffle } from 'lodash';
import { Subscription } from 'rxjs/internal/Subscription';
// Services
// Widgets model
import { LayoutConfigService, SparklineChartOptions } from '../../../core/_base/layout';
import globalVariables from '../globalVariables';

@Component({
	selector: 'gas-employees-actions',
	templateUrl: 'employees-actions.component.html',
	styleUrls: ['employees-actions.component.scss'],
})
export class EmployeesActionsComponent implements OnInit {
	employees = [];

	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	constructor(private layoutConfigService: LayoutConfigService, private store: Store<AppState>,
				private http: HttpClient) {
	}

	ngOnInit(): void {
		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.getEmployees();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}


	getEmployees() {
		this.http.get(
			globalVariables.baseUrl + 'api/Users',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.employees = res.data.map(item => {
				return {
					name: item.name,
					id: item.userId
				}
			});
		});
	}
}
