// Angular
import {Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
// Material
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
// RxJS
import {Subscription, of} from 'rxjs';
import {delay} from 'rxjs/operators';
// NGRX
import {Update} from '@ngrx/entity';
import {Store, select} from '@ngrx/store';
// State
import {AppState} from '../../../../../core/reducers';
// CRUD
import {LayoutUtilsService, TypesUtilsService} from '../../../../../core/_base/crud';
// Services and Models
import {selectCustomersActionLoading} from '../../../../../core/e-commerce';
import {AdvancedPaymentModel} from '../models/advanced-payment.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'advanced-payment-edit-dialog',
	templateUrl: 'advanced-payment-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class AdvancedPaymentEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	AdvancedPayment: AdvancedPaymentModel;
	AdvancedPaymentForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	fuelTypes = [];
	employees = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';


	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param typesUtilsService: TypesUtilsService
	 */
	constructor(public dialogRef: MatDialogRef<AdvancedPaymentEditDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private fb: FormBuilder,
				private store: Store<AppState>,
				private layoutUtilsService: LayoutUtilsService,
				private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.AdvancedPayment = this.data.reading;
		this.employees = this.data.employees;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}

		this.subscriptions.forEach(el => el.unsubscribe());
	}

	createForm() {
		this.AdvancedPaymentForm = this.fb.group({
			id: [this.AdvancedPayment.id, Validators.required],
			userId: [this.AdvancedPayment.userId, Validators.required],
			advancedDetails: [this.AdvancedPayment.advancedDetails, Validators.required],
			amount: [this.AdvancedPayment.amount, Validators.required],
			advancedPaymentStatusId: [this.AdvancedPayment.advancedPaymentStatusId, this.AdvancedPayment.id > 0 ? Validators.required : ''],
			adminReply: [this.AdvancedPayment.adminReply, this.AdvancedPayment.id > 0 ? Validators.required : ''],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.AdvancedPayment.id > 0) {
			return `تعديل الحركة`;
		}

		return 'اضافة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.AdvancedPaymentForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): AdvancedPaymentModel {
		const controls = this.AdvancedPaymentForm.controls;
		const _AdvancedPayment = new AdvancedPaymentModel();
		_AdvancedPayment.id = this.AdvancedPayment.id;
		_AdvancedPayment.userId = controls.userId.value;
		_AdvancedPayment.advancedDetails = controls.advancedDetails.value;
		_AdvancedPayment.amount = controls.amount.value;
		if (this.AdvancedPayment.id > 0) {
			_AdvancedPayment.adminReply = controls.adminReply.value;
			_AdvancedPayment.advancedPaymentStatusId = controls.advancedPaymentStatusId.value;
		}

		return _AdvancedPayment;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.AdvancedPaymentForm.controls;
		console.log(this.AdvancedPaymentForm);

		/** check form */
		if (this.AdvancedPaymentForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel price
	 *
	 * @param _AdvancedPayment: FuelPricesModel
	 */
	updateReading(_AdvancedPayment: AdvancedPaymentModel) {
		this.http.put(
			globalVariables.baseUrl + 'api/AdvancePayment',
			{
				..._AdvancedPayment
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_AdvancedPayment, isEdit: true});
			}, err => {
				let msg = 'فشل في تعديل السلفة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Fuel price
	 *
	 * @param _AdvancedPayment: FuelPricesModel
	 */
	createReading(_AdvancedPayment: AdvancedPaymentModel) {
		this.http.post(
			globalVariables.baseUrl + 'api/AdvancePayment',
			{
				..._AdvancedPayment
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_AdvancedPayment, isEdit: false});
			}, err => {
				let msg = 'فشل في اضافة السلفة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
