// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { OilSalesModel } from '../models/oil-sales.model';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'oil-sales-edit-dialog',
	templateUrl: 'oil-sales-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class OilSalesEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	oilSales: OilSalesModel;
	oilSalesForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	fuelTypes = [];
	// Private properties
	private componentSubscriptions: Subscription;

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param typesUtilsService: TypesUtilsService
	 */
	constructor(public dialogRef: MatDialogRef<OilSalesEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private typesUtilsService: TypesUtilsService) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.oilSales = this.data.reading;
		this.fuelTypes = this.data.fuelTypes;
		this.createForm();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		this.oilSalesForm = this.fb.group({
			id: [this.oilSales.id, Validators.required],
			category: [this.oilSales.category, Validators.required],
			price: [this.oilSales.price, Validators.required],
			amount: [this.oilSales.amount, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.oilSales.id > 0) {
			return `تعديل الحركة '${this.oilSales.category}'`;
		}

		return 'اضافة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.oilSalesForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): OilSalesModel {
		const controls = this.oilSalesForm.controls;
		const _oilSales = new OilSalesModel();
		_oilSales.id = this.oilSales.id;
		_oilSales.category = controls.category.value;
		_oilSales.price = controls.price.value;
		_oilSales.amount = controls.amount.value;

		return _oilSales;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.oilSalesForm.controls;
		console.log(this.oilSalesForm);

		/** check form */
		if (this.oilSalesForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel price
	 *
	 * @param _oilSales: FuelPricesModel
	 */
	updateReading(_oilSales: OilSalesModel) {
		const updateCustomer: Update<OilSalesModel> = {
			id: _oilSales.id,
			changes: _oilSales
		};
		// hit api to edit reading
		this.dialogRef.close({ _oilSales, isEdit: true });
	}

	/**
	 * Create Fuel price
	 *
	 * @param _oilSales: FuelPricesModel
	 */
	createReading(_oilSales: OilSalesModel) {
		// hit api to create reading
		this.dialogRef.close({ _oilSales, isEdit: false });
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
